import React from 'react';

class SearchBar extends React.Component{

    state = {item: ''};

    OnInputChange(event){
        this.setState({item:event.target.value});
    }

    /*--- To communicate from a child to  parent use callbacks ---*/
    onFormSubmit = (event) =>{
        event.preventDefault();
        //console.log("Just submitted the form!!");
        // console.log(this.state.item);
        this.props.OnSubmit(this.state.item);
    }

    render(){
        return(
            <div className="ui segment" style={{paddingTop:'10px'}}>
                <form className="ui form" onSubmit={this.onFormSubmit}>
                    <div className="ui form">
                        <div className="field">
                        <h1><label>Image Search</label></h1>
                        <input type="text" value={this.state.item} onChange={(event)=> this.OnInputChange(event)} />
                        </div>
                    </div>
                </form>
            </div>

        );
    }
    /*------ Controlled Components -> In a controlled component, the form data is handled by the React component.
    
    Uncontrolled Components -> In an uncontrolled component, the form data is handled by the DOM itself.
    */
}

export default SearchBar;